.post_wrapper {
	background-color: #ffffff;

	.backbutton {
		a {
			display: inline-block;
			transform: translate(10px, 10px);
			color: #2b7a78;
			text-decoration: none;
			margin-right: 30px;
			font: {
				size: 20px;
				family: 'Roboto Mono', monospace; }

			img {
				width: 30px;
				height: auto; }

			&:hover {
				position: relative;
				left: -0.2vw;
				color: rgba(0,0,0,0.4); } } }

	.post_inner {
		max-width: 800px;
		margin-left: 5%;
		margin-right: 10%;
		padding: 0 25px;

		.main_info {
			background-color: #def2f1;
			margin: 30px 0;
			padding: 5px 15px;
			border-bottom: 1px solid;
			font-family: 'Roboto Mono', monospace;
			h1 {
				font-size: 35px; }
			h3 {
				color: gray;
				font: {
					size: 20px;
					weight: 300; } }
			p {
				text-align: right;
				color: gray;
				font: {
					size: 15px;
					family: 'Open Sans', sans-serif; } } }

		.main_post {
			font-family: 'Open Sans', sans-serif;
			line-height: 150%;

			a {
				text-decoration: none;
				color: #f76c6c;
				font: {
					size: 15px;
					family: 'Roboto Mono', monospace; }

				&:hover {
					color: rgba(0,0,0,0.4); } }

			h2 {
				font-size: 31.25px;
				font-family: 'Roboto Mono', monospace; }
			h3 {
				font-size: 25px;
				font-family: 'Roboto Mono', monospace; }
			h4 {
				font-size: 20px;
				font-family: 'Roboto Mono', monospace; }
			h5 {
				font-size: 18px;
				font-family: 'Roboto Mono', monospace; }
			p {
				font-size: 16px; }

			img {
				max-width: 100%; }

			embed {
				max-width: 100%;
				height: 600px; }

			.marginnote {
				float: right;
				clear: right;
				margin-right: -60%;
				width: 50%;
				margin-top: 0.3rem;
				margin-bottom: 0;
				font-size: 0.75rem;
				line-height: 1.3;
				vertical-align: baseline;
				position: relative;
				padding-right: 7px;
				font-family: 'Montserrat', sans-serif; }

			.progress {
				position: relative;
				margin-top: 10px;
				margin-bottom: 30px;
				height: 10px;
				width: 90%;
				border: 2px solid black;
				border-radius: 15px;

				.color {
					position: absolute;
					background-color: #6cf76c;
					height: 10px;
					border-radius: 15px; } }

			.footnotes {
				hr {
					margin: 40px 20px 20px 20px; }
				h4 {
					font-size: 16px; }
				p {
					font-size: 12px; }
				ol {
					padding: 0;
					list-style: none; } } } } }










